

.footer{
    padding: 50px 0;
    box-shadow: 0px 0px 79px #f3f3f3;
}

.footer .social-box{
    margin-left: auto;
}

.footer h4{
    font-weight: 400;
    color: #8d8d8d;
}

.footer h4 a {
    color: #181818;
    text-decoration: none;
    transition: 0.2s;
}

.footer h4 a:hover{
    color: #8d8d8d;
}


@media screen and (max-width: 767px) {
    .footer{
        padding: 25px 0 25px 0; 
        text-align: center;
    }

    .footer .social-box{
        margin:20px auto 0 auto;
    }
}