

.sc-banner{
    padding-top: 50px;
    padding-bottom: 50px;
    color:#2b2b2b;
    position: relative;
}

.sc-banner .h1-title-box:before {
    content: "Hello";
    top: 75%;
    left: -200px;
}

.sc-banner p{
    font-size: 18px;
    overflow: hidden;
}

.sc-banner p span {
    display: inline-block;
}

.sc-banner .img-box{
    max-width: 450px;
    margin-top: 0px;
    position: relative;
}

.sc-banner .img-box img{
    max-width: 100%;
}

@media screen and (max-width: 1470px) {

    .sc-banner p{
        max-width: 500px;
    }
    

    .sc-banner .img-box {
        margin-top: 31px;
        max-width: 33vw;
    }
}

@media screen and (max-width: 767px) {

    .sc-banner .img-box-container{
        display: none;
    }

    .sc-banner p{
        font-size: 14px;
    }

    .sc-banner p br{
        display: none;
    }

    .sc-banner .h1-title-box:before {
        top: -29%;
        left: 10%;
    }
    

}

@media screen and (max-width: 575px){
.sc-banner .h1-title-box::before{
    top: 30%;
  }
}