
.header{
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 9999;
}

.header .social-box{
    margin: auto;
}

.header .navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 110px;
    align-items: center;
    color:#2b2b2b;
    user-select: none;
}

.header .logo-box{
    font-family: "Maven Pro";
    font-weight: 700;
    font-size: 39px;
    position: relative;
}

.header .logo-box a{
    text-decoration: none;
    color:#2b2b2b;
}

.header .logo-box::before{
    content: "";
    position: absolute;
    bottom: 5px;
    right: -14px;
    width: 10px;    
    height: 10px;
    border-radius: 100%;
    background-color: #fed9c8;
}

.header .menu-box{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    position: relative;
    justify-content: right;
    align-items: center;
}

.header .menu-list{
    padding-left: 0;
    list-style: none;
    width: 250px;
    position: absolute;
    right: 0;
    height: 100vh;
    top: 0px;
    box-shadow: 0px 3px 20px #c0c0c0;
    position: fixed;
    z-index: 999;
    background-color: #fff;
    transition: 0.4s ease-in-out;
    padding-top: 110px;
    padding-left: 60px;
    text-align: left;
}



.header .menu-text{
    font-family: "Maven Pro";
    font-weight: 500;
    margin-right: 10px;
    font-size: 16px;
    position: relative;
    top: -7px;
}

.header .burger-icon{
    border: none;
    background: none;
    font-size: 25px;
    line-height: 0;
    cursor: pointer;
    position: relative;
}

.header .close-menu-list{
    border: none;
    background: none;
    font-weight: 500;
    top: 36px;
    right: 28px;
    line-height: 0;
    cursor: pointer;
    position: relative;
    z-index: 9999;
    position: absolute;
    left: 0;
    margin: auto;
    text-align: center;
    
}

.header .close-menu-list svg{
    font-size: 25px;
    position: relative;
    top: 7px;
}

.header .menu-list li{
    width: 100%;
    padding: 15px 25px;
    text-align: right;
}

.header .menu-list li a:hover{
    color:#2b2b2b;
}

.header .menu-list li a:hover:before{
    width: 100%;
}

.header .menu-list li a::before{
    content: "";
    position: absolute;
    top: 7px;
    border-radius: 5px;
    right: 0;
    bottom: 0;
    margin: auto;
    height: 7px;
    width: 0;
    background-color: #fcc9b1;
    z-index: -1;
    transition: 0.5s;
}

.header .menu-list li a{
    font-weight: 500;
    transition: 0.2s;
    color:#adacac;
    font-size: 35px;
    text-decoration: none;
    position: relative;

}

.header .menu-list li a.active{
    color:#2b2b2b;
}

.header .menu-list li a.active:before{
    content: "";
    position: absolute;
    top: 7px;
    border-radius: 5px;
    right: 0;
    bottom: 0;
    margin: auto;
    height: 7px;
    width: 100%;
    background-color: #fcc9b1;
    z-index: -1;
    transition: 0.5s;
}

@media screen and (max-width: 1700px){

    .header .close-menu-list {
        right: 0px;
        text-align: right;
        padding-right: 8px;
    }

    .header .menu-list li {
        padding: 15px 13px;
    }


}



@media screen and (max-width: 540px) {

    .header .navbar {
        height: 78px;
    }

    .header .menu-text {
        display: none;
    }

    .header .burger-icon {
        margin-top: 2px;
    }

}