
.social-box{
    max-width: 250px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.social-box a{
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    background-color: #fff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 100%;
    margin: 0 10px;
}

.social-box a:hover{
    box-shadow:inset 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.social-box img{
    transform: scale(0.8);
    max-width: 17px;
}


@media screen and (max-width: 540px) {

    .social-box a{
        width: 20px;
        height: 20px;
    }

    .social-box img {
        transform: scale(0.5);
    }

}
