/* bold  700 */
@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@700&display=swap');
/* medium 500 */
@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;600;700&display=swap');
/* regular 400 */
@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;600;700&display=swap');

/* ubunto bold  */
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#root{
  padding: 25px;
}

#wrapper{
  padding-top: 110px;
  overflow: hidden;
  box-shadow: 0px 0px 30px #dddddd;
  border-radius: 10px;
  background: #f8f8f8;
}

.motion-logo{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 15px;
  background-color: black;
}

 
body{
  background: #fff;
  font-family: "Maven Pro";
  font-weight: 400;
}

.content-col{
  color:#fff;
}

p{
  color: #747474;
}

::-webkit-scrollbar {
  width: 8px;
  
}
/* Track */
::-webkit-scrollbar-track {
  background: #fff; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.377); 
  border-radius: 10px;

}

h1{
  color:#2b2b2b;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 700;
  font-size: 10vw;
  position: relative;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
}

h1 span{
  display: inline-block;
}

h1 span:last-child{
  color: #fed9ca;
}

.h1-title-box{
  position: relative;
  display: inline-block;
}

.h1-title-box:before{
    content: "";
    font-size: 28vw;
    color: #fbfbfb;
    font-weight: 700;
    position: absolute;
    top: -29%;
    bottom: 0%;
    margin: auto;
    left: 10%;
    z-index: -1;
    display: inline-block;
}


h2{
  color:#2b2b2b;
  font-family: "Maven Pro";
  font-weight: 700;
  font-size: 47px;
}

h2 span{
  position: relative;
}

h2 span::before{
  content: "";
  position: absolute;
  top: 12px;
  border-radius: 5px;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  height: 10px;
  width: 100%;
  background-color: #fcc9b1;
  z-index: -1;
}

.container{
  max-width: 1730px;
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 50px;
  padding-right: 50px;
}


.text-left{
  text-align: left;
  display: flex;
  flex-wrap: wrap;
}

.text-right{
  text-align: right;
  display: flex;
  flex-wrap: wrap;
}

.text-center{
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.col-row{
  display: flex;
  flex-wrap: wrap;
  margin-right: -10px;
  margin-left: -10px;
}

.col-100{
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
}

.col-33{
  width: 33.3%;
  padding-left: 10px;
  padding-right: 10px;
}

.col-50{
  width: 50%;
  padding-left: 10px;
  padding-right: 10px;
}

.pending-style{
  font-size: 20px;
  color: #000;
  margin: 10px 0;
  font-weight: bold;
}

.primary--button{
  padding: 10px 20px;
  background-color: #181818;
  color:#fff;
  text-align: center;
  text-decoration: none;
  outline: none;
  font-weight: 400;
  font-size: 16px;
  display: inline-block;
  border: solid 1px #181818;
  transition: 0.2s;
}

.primary--button:hover{
  background-color: #fff;
  color: #181818;
}

.modal-overlay{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.719);
  display: flex;
  align-items: center;
  justify-content: center;
} 

.modal-content{
  max-width: 600px;
  width: calc(100% - 20px);
  background-color: #fff;
  padding: 10px 15px;
  border-radius: 10px;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  align-self: center;
  margin: 80px auto;
  z-index: 999;
  position: relative;
  transition: 0.5s;
} 

.modal-tabs-content{
  max-height: 60vh;
  overflow: auto;
}

.modal-img-box{
  display: inline-block;
  width: 100%;
}
.modal-tabs{
  padding-top: 50px;
}

.modal-tabs-ruler{
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 5px;
}

.modal-tabs-ruler button{
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  border: none;
  width:50%;
  padding: 10px 15px 5px 15px;
  cursor: pointer;
  margin: 5px;
  border-bottom: 5px solid transparent;
}

.modal-tabs-ruler button.active-tab{
  border-color: rgb(255, 0, 0);
  background-color: #fff;
  color: #000;
}

.modal-img-box img{
  max-width: 100%;
}

.close-modal{
  position: absolute;
  top: -50px;
  right: -50px;
  width: 50px;
  height: 50px;
  font-size: 25px;
  background-color: #fff;
  border:none;
  border-radius: 100%;
  cursor: pointer;
  transition: 0.2s;
  font-weight: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-modal:hover{
  opacity: 0.7
}

@media screen and (max-width: 1200px) {
  .col-33{
    width: auto;
  }
}


@media screen and (max-width: 767px){
  
   .container{
      padding-left: 15px;
      padding-right: 15px;
    }

    .col-50{
      width: 100%;
    }

    h2{
      font-size: 31px;
    }

    h2 span:before{
      top: 9px;
      height: 5px;
    }

    .modal-content{
      margin: 58px auto;
    }

    .close-modal{
      top: -41px;
      right: -5px;
      width: 30px;
      height: 30px;
      font-size: 20px;
    }

    #root{
      padding: 15px;
    }
    
}


@media screen and (max-width: 575px){
  
  h1 {
    font-size: 59px;
  }
  .h1-title-box{
    width: 100%;
  }
  .h1-title-box::before{
    top: 30%;
  }

}



