


.sc-error-404{
    padding-top: 50px;
    padding-bottom: 50px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background-color: #fff;
    display: flex;
    align-items: center;
    overflow-x: auto;
}

.sc-error-404 .error-content{
    position: relative;
    z-index: 1;
}

.sc-error-404 h1{
    font-size: 250px!important;
}

.sc-error-404 h2 {
    font-size: 70px;
}

.sc-error-404 p{
    color:#2b2b2b;
}

.sc-error-404 .circle{
    width: 100vh;
    height: 100vh;
    top: -50vh;
    position: absolute;
    right: 0;
    border-radius: 100%;
    border: solid 150px #FBFBFB;

}

.sc-error-404 .menu-list{
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    padding-right: 100px;
    text-align: right;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 20px;
    height: 100%;
}

.sc-error-404 .menu-list li{
    width: 100%;
    display: flex;  
    align-items: center;
    flex-wrap: wrap;
    justify-content: right;
}

.sc-error-404 .menu-list li a{
    font-size: 120px;
    font-weight: 700;
    transition: 0.2s;
    color:#adacac;
    text-decoration: none;
    position: relative;
    z-index: 1;

}

.sc-error-404 .menu-list li a::before{
    content: "";
    position: absolute;
    top: 20px;
    border-radius: 55px;
    right: 0;
    bottom: 0;
    margin: auto;
    height: 30px;
    width: 0;
    background-color: #fcc9b1;
    z-index: -1;
    transition: 0.5s;
}

.sc-error-404 .menu-list li a:hover{
    color:#2b2b2b;
}

.sc-error-404 .menu-list li a:hover:before{
    width: 100%;
}

@media screen and (max-width: 1200px) {
    .sc-error-404 h1 {
        font-size: 10vw!important;
    }
    .sc-error-404 .menu-list li a {
        font-size: 56px;
    }
    .sc-error-404 h2 {
        font-size: 44px;
        margin-bottom: 10px;
    }
    .sc-error-404 .menu-list{
        padding-right: 50px;
    }
}

@media screen and (max-width: 767px) {
    .sc-error-404 .menu-list{
        padding-right: 15px;
    }
}

@media screen and (max-width: 576px) {
    .sc-error-404 .error-content {
        max-width: 54%;
    }
    .sc-error-404 h1 {
        font-size: 60px!important;
    }
    .sc-error-404 .menu-list li a {
        font-size: 29px;
    }
    .sc-error-404 h2 {
        font-size: 23px;
    }
}