

.sc-contact{
    position: relative;
    padding: 50px 0;
    
}

.sc-contact h2{
    padding: 30px 0;
}


.sc-contact .contact-box{
    display: flex;
    width: 100%;
    padding: 20px 0;
}

.sc-contact .contact-box div{
    margin-right: 25px;
}

.sc-contact .contact-box h3{
    color:#181818;
    font-weight: 700;
    margin-bottom: 5px;
    font-size: 30px;
}


.sc-contact .contact-box p a{
    color: #181818;
    font-size: 16px;
}

.sc-contact .h1-title-box:before {
    content: "Contact";
}

.sc-contact .form-box{
    display: inline-block;
    width: 60%;
}

.sc-contact .field-box{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.sc-contact .text-box{
    display: flex;
    width: 100%;
}

.sc-contact input[type=text], .sc-contact input[type=email], .sc-contact textarea{
    background-color: #fff;
    color: #747474;
    padding: .63rem 1.25rem;
    border: 0;
    font-size: .88rem;
    line-height: 2.14;
    border-bottom: 1px solid transparent;
    transition: border-color .15s ease-in-out;
    font-weight: 500;
    outline: none;
}


.sc-contact .field-box{
    margin: 10px 0;
  }

.sc-contact input[type=text]:focus, .sc-contact input[type=email]:focus, .sc-contact textarea:focus{
    border-bottom: 1px solid #181818;
}

.sc-contact input[type=text],
.sc-contact input[type=email]{
    width: 49.5%;
}

.sc-contact textarea{
    width: 100%;
    resize: vertical;
    min-height: 260px;
}

.sc-contact .primary--button{
    cursor: pointer;
    margin-top: 1%;
    min-width: 200px;
}

@media screen and (max-width: 1200px) {
    .sc-contact .form-box {
        width: 100%;
    }
}

@media screen and (max-width: 575px) {
    .sc-contact .field-box {
        width: 100%;
        flex-wrap: wrap;
        margin: 0;
    }
    .sc-contact .field-box input[type=text],
    .sc-contact .field-box input[type=email]{
        width: 100%;
        margin: 5px 0;
    }

    .sc-contact .text-box{
        margin: 5px 0;
    }
    .sc-contact textarea {
        min-height: 130px;
    }

    .sc-contact .contact-box {
        flex-wrap: wrap;
        padding-top: 0;
        padding-bottom: 0;
    }

    .sc-contact .contact-box div {
        width: 100%;
        margin: 10px 0;
    }

    .sc-contact .contact-box h3 {
        font-size: 18px;
    }
    .sc-contact .contact-box p a {
        font-size: 13px;
    }
}