*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#root{
  padding: 25px;
}

#wrapper{
  padding-top: 110px;
  overflow: hidden;
  box-shadow: 0px 0px 30px #dddddd;
  border-radius: 10px;
  background: #f8f8f8;
}

.motion-logo{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 15px;
  background-color: black;
}

 
body{
  background: #fff;
  font-family: "Maven Pro";
  font-weight: 400;
}

.content-col{
  color:#fff;
}

p{
  color: #747474;
}

::-webkit-scrollbar {
  width: 8px;
  
}
/* Track */
::-webkit-scrollbar-track {
  background: #fff; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.377); 
  border-radius: 10px;

}

h1{
  color:#2b2b2b;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 700;
  font-size: 10vw;
  position: relative;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
}

h1 span{
  display: inline-block;
}

h1 span:last-child{
  color: #fed9ca;
}

.h1-title-box{
  position: relative;
  display: inline-block;
}

.h1-title-box:before{
    content: "";
    font-size: 28vw;
    color: #fbfbfb;
    font-weight: 700;
    position: absolute;
    top: -29%;
    bottom: 0%;
    margin: auto;
    left: 10%;
    z-index: -1;
    display: inline-block;
}


h2{
  color:#2b2b2b;
  font-family: "Maven Pro";
  font-weight: 700;
  font-size: 47px;
}

h2 span{
  position: relative;
}

h2 span::before{
  content: "";
  position: absolute;
  top: 12px;
  border-radius: 5px;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  height: 10px;
  width: 100%;
  background-color: #fcc9b1;
  z-index: -1;
}

.container{
  max-width: 1730px;
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 50px;
  padding-right: 50px;
}


.text-left{
  text-align: left;
  display: flex;
  flex-wrap: wrap;
}

.text-right{
  text-align: right;
  display: flex;
  flex-wrap: wrap;
}

.text-center{
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.col-row{
  display: flex;
  flex-wrap: wrap;
  margin-right: -10px;
  margin-left: -10px;
}

.col-100{
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
}

.col-33{
  width: 33.3%;
  padding-left: 10px;
  padding-right: 10px;
}

.col-50{
  width: 50%;
  padding-left: 10px;
  padding-right: 10px;
}

.pending-style{
  font-size: 20px;
  color: #000;
  margin: 10px 0;
  font-weight: bold;
}

.primary--button{
  padding: 10px 20px;
  background-color: #181818;
  color:#fff;
  text-align: center;
  text-decoration: none;
  outline: none;
  font-weight: 400;
  font-size: 16px;
  display: inline-block;
  border: solid 1px #181818;
  transition: 0.2s;
}

.primary--button:hover{
  background-color: #fff;
  color: #181818;
}

.modal-overlay{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.719);
  display: flex;
  align-items: center;
  justify-content: center;
} 

.modal-content{
  max-width: 600px;
  width: calc(100% - 20px);
  background-color: #fff;
  padding: 10px 15px;
  border-radius: 10px;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  align-self: center;
  margin: 80px auto;
  z-index: 999;
  position: relative;
  transition: 0.5s;
} 

.modal-tabs-content{
  max-height: 60vh;
  overflow: auto;
}

.modal-img-box{
  display: inline-block;
  width: 100%;
}
.modal-tabs{
  padding-top: 50px;
}

.modal-tabs-ruler{
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 5px;
}

.modal-tabs-ruler button{
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  border: none;
  width:50%;
  padding: 10px 15px 5px 15px;
  cursor: pointer;
  margin: 5px;
  border-bottom: 5px solid transparent;
}

.modal-tabs-ruler button.active-tab{
  border-color: rgb(255, 0, 0);
  background-color: #fff;
  color: #000;
}

.modal-img-box img{
  max-width: 100%;
}

.close-modal{
  position: absolute;
  top: -50px;
  right: -50px;
  width: 50px;
  height: 50px;
  font-size: 25px;
  background-color: #fff;
  border:none;
  border-radius: 100%;
  cursor: pointer;
  transition: 0.2s;
  font-weight: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-modal:hover{
  opacity: 0.7
}

@media screen and (max-width: 1200px) {
  .col-33{
    width: auto;
  }
}


@media screen and (max-width: 767px){
  
   .container{
      padding-left: 15px;
      padding-right: 15px;
    }

    .col-50{
      width: 100%;
    }

    h2{
      font-size: 31px;
    }

    h2 span:before{
      top: 9px;
      height: 5px;
    }

    .modal-content{
      margin: 58px auto;
    }

    .close-modal{
      top: -41px;
      right: -5px;
      width: 30px;
      height: 30px;
      font-size: 20px;
    }

    #root{
      padding: 15px;
    }
    
}


@media screen and (max-width: 575px){
  
  h1 {
    font-size: 59px;
  }
  .h1-title-box{
    width: 100%;
  }
  .h1-title-box::before{
    top: 30%;
  }

}





.header{
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 9999;
}

.header .social-box{
    margin: auto;
}

.header .navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 110px;
    align-items: center;
    color:#2b2b2b;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.header .logo-box{
    font-family: "Maven Pro";
    font-weight: 700;
    font-size: 39px;
    position: relative;
}

.header .logo-box a{
    text-decoration: none;
    color:#2b2b2b;
}

.header .logo-box::before{
    content: "";
    position: absolute;
    bottom: 5px;
    right: -14px;
    width: 10px;    
    height: 10px;
    border-radius: 100%;
    background-color: #fed9c8;
}

.header .menu-box{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    position: relative;
    justify-content: right;
    align-items: center;
}

.header .menu-list{
    padding-left: 0;
    list-style: none;
    width: 250px;
    position: absolute;
    right: 0;
    height: 100vh;
    top: 0px;
    box-shadow: 0px 3px 20px #c0c0c0;
    position: fixed;
    z-index: 999;
    background-color: #fff;
    transition: 0.4s ease-in-out;
    padding-top: 110px;
    padding-left: 60px;
    text-align: left;
}



.header .menu-text{
    font-family: "Maven Pro";
    font-weight: 500;
    margin-right: 10px;
    font-size: 16px;
    position: relative;
    top: -7px;
}

.header .burger-icon{
    border: none;
    background: none;
    font-size: 25px;
    line-height: 0;
    cursor: pointer;
    position: relative;
}

.header .close-menu-list{
    border: none;
    background: none;
    font-weight: 500;
    top: 36px;
    right: 28px;
    line-height: 0;
    cursor: pointer;
    position: relative;
    z-index: 9999;
    position: absolute;
    left: 0;
    margin: auto;
    text-align: center;
    
}

.header .close-menu-list svg{
    font-size: 25px;
    position: relative;
    top: 7px;
}

.header .menu-list li{
    width: 100%;
    padding: 15px 25px;
    text-align: right;
}

.header .menu-list li a:hover{
    color:#2b2b2b;
}

.header .menu-list li a:hover:before{
    width: 100%;
}

.header .menu-list li a::before{
    content: "";
    position: absolute;
    top: 7px;
    border-radius: 5px;
    right: 0;
    bottom: 0;
    margin: auto;
    height: 7px;
    width: 0;
    background-color: #fcc9b1;
    z-index: -1;
    transition: 0.5s;
}

.header .menu-list li a{
    font-weight: 500;
    transition: 0.2s;
    color:#adacac;
    font-size: 35px;
    text-decoration: none;
    position: relative;

}

.header .menu-list li a.active{
    color:#2b2b2b;
}

.header .menu-list li a.active:before{
    content: "";
    position: absolute;
    top: 7px;
    border-radius: 5px;
    right: 0;
    bottom: 0;
    margin: auto;
    height: 7px;
    width: 100%;
    background-color: #fcc9b1;
    z-index: -1;
    transition: 0.5s;
}

@media screen and (max-width: 1700px){

    .header .close-menu-list {
        right: 0px;
        text-align: right;
        padding-right: 8px;
    }

    .header .menu-list li {
        padding: 15px 13px;
    }


}



@media screen and (max-width: 540px) {

    .header .navbar {
        height: 78px;
    }

    .header .menu-text {
        display: none;
    }

    .header .burger-icon {
        margin-top: 2px;
    }

}

