.sc-my-works{
    padding-top: 50px;
    padding-bottom: 50px;
}

.works-tabel .work-tags{
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    list-style: none;
    width: 100%;
    justify-content: center;
    margin-bottom: 35px;
    padding-top: 60px;
}

.works-tabel .active-btn{
    text-decoration: underline;
}

.works-tabel .work-tags li{
    position: relative;
}

.works-tabel .work-tags li:before{
    content: "";
    right: -2px;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 5px;
    height: 1px;
    background: #b1b1b1;
}
.works-tabel .work-tags li:last-child:before{
    display: none;
}

.works-tabel .work-tags button{
    background: none;
    border: none;
    margin: 0 10px;
    color: #747474;
    position: relative;
    cursor: pointer;
    
}

.works-tabel .work-image{
   background-size: 100%;
}

.works-tabel .work-box{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    margin-top: 15px;
    
}

.works-tabel .single-work{
    width: 25%;
    overflow: hidden;
    position: relative;
}

.works-tabel .single-work:hover img{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.works-tabel .work-view{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.5);
    transition: 0.2s;
    box-shadow: none;
    outline: none;
    border: none;
    cursor: pointer;
}

.works-tabel .modal-link-def{
    background-color: rgb(136, 15, 15);
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.works-tabel .work-link{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 40px;
    background-color: #181818;
    color:#fff;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    text-decoration: none;
 
}

.works-tabel .work-link:hover{
    text-decoration: none;
}

.works-tabel .work-view span{
    text-transform: uppercase;
    width: 100px;
    height: 100px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    font-size: 12px;
    text-align: center;
    color:#fff;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #181818;
    border-radius: 100%;
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: 0.2s;
}

.works-tabel .work-view:hover span{
    -webkit-transform: scale(1);
            transform: scale(1);
}

.works-tabel .work-view:hover{
    opacity: 1;
}

.works-tabel .work-box img{
    width: 100%;
    transition: 0.2s;
    
}

