
.sc-about{
    padding: 50px 0;
    color: #181818;
    position: relative;
}

.sc-about .h1-title-box:before {
    content: "About";
}

.sc-about .self-info{
    padding-top: 50px;
}

.sc-about .title{
    padding-left: 8%;
    display: inline-block;
    width: 100%;
}

.sc-about .info-box{
    display: flex;
    width: 100%;
    padding-left: 10%;
    padding-right: 10%;
    padding-top: 50px;
    align-items: center;
}

.sc-about .info-box .img-box img{
    max-width: 100%;
}

.sc-about .info-box .text-box {
    max-width: 350px;
    display: inline-block;
    padding-left: 20px;
}

.sc-about .info-box .text-box h4{
    margin: 10px 0;
}

.sc-about .info-box .text-box h3{
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 20px;
}

.sc-about .info-box .other-info{
    margin-top: 30px;
}

.sc-about .info-box .text-box p{
    font-size: 16px;
    line-height: 30px;
}

.sc-about .skill-stats{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    user-select: none;
}

.sc-about .single-skill{
    width: 64px;
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 15px 15px;
    position: relative;
}

.sc-about .single-skill img{
    max-width: 100%;
}

.sc-about .basic{
    position: absolute;
    font-weight: 500;
    width: 35px;
    height: 35px;
    top: -10%;
    right: -5%;
    font-size: 11px;
    display: flex;
    align-items: center;
    border-radius: 100%;
    justify-content: center;
    background-color: rgb(241, 241, 32);
}

@media screen and (max-width: 1200px) {
    .sc-about .img-box-container{
        display: none;
    }

    .sc-about .info-box{
        align-items: flex-start;
    }

    .sc-about .col-33{
        width: 50%;
    }
}

@media screen and (max-width: 767px) {

    .sc-about .title{
        padding-left: 0;
    }

    .sc-about .info-box{
        padding-left: 0;
        padding-right: 0;
        padding-top: 30px;
    }

    .sc-about .info-box .text-box{
        padding-left: 0;
       margin: 20px 0;
    }

    .sc-about .single-skill {
        margin: 15px 10px;
    }

    .sc-about .info-box .text-box p {
        line-height: 21px;
    }

    .sc-about .info-box .text-box h3 {
        font-size: 18px;
        margin-bottom: 10px;
    }

    .sc-about .info-box .text-box h4 {
        font-size: 18px;
    }

}


@media screen and (max-width: 575px) {

    .sc-about .info-box{
        flex-wrap: wrap;
    }

    .sc-about .col-33{
        width: 100%;
    }

    .sc-about .skill-stats{
        justify-content: space-between;
    }

}